import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';

// key, en, zh
type SingleKeyT = [string, string, string]
type SingleLangT = {translation: {[key: string]: string}};
type KeyTranslations = {
  zh: SingleLangT
  en: SingleLangT
};

function translate(translations: SingleKeyT[]): KeyTranslations {
  var result: KeyTranslations = {zh: {translation: {}}, en: {translation: {}}}
  for(const t of translations) {
    const [k, en, zh] = t
    result.en.translation[k] = en
    result.zh.translation[k] = zh
  }
  return result
}

const translations: KeyTranslations = translate([
  // Actions
    ["action.edit", "Edit", ""],
    ["action.delete", "Delete", ""],
    ["action.login", "Login", ""],
    ["action.logout", "Logout", ""],
    ["action.save", "Save", ""],
    ["action.cancel", "Cancel", ""],
    ["action.add_note", "Add Note", ""],

    ["sync.success", "Sync success", ""],
    ["sync.failure", "Sync failed. please try again later", ""],

    ["login.failed", "Login failed, please check username & password", ''],
    ["login.succeeded", "Login successful", ''],

    ["note.updated_at", "Updated at: {{time}}", "{{time}}"],
    ["note.created_at", "Created at: {{time}}", "{{time}}"],

  // Login Prompt
    ["username", "Username", ""],
    ["password", "Password", ""],
    ["my_notes", "My Notes", ""],

    ["to", "to", ""],
    ["creation_time", "Creation Time", "" ],
    ["modification_time", "Modification Time", "" ],

    ["sync.result_success", "Sync successful", ""],
    ["sync.result_failure", "Sync failed, please try again later", ""],
    ["sync.syncing", "Syncing...", "..."],
    ["sync.sync_notes", "Sync Notes", ""],
    ["sync.last_x_days", "recent {{days}} days", "{{days}}"],
    ["sync.all_days", "All Days", ""],
    ["sync.syncing", "syncing...", ""],
    ["sync.pull_to_sync", "Pull to Sync", ""],

    ["timing.num_unit_ago", "{{num}} {{unit}} ago", "{{num}}{{unit}}"],
    ["timing.just_now", "just now", ""],
    ["timing.unit_hour", "hour", ""],
    ["timing.unit_minute", "minute", "" ],
    ["timing.unit_day", "day", ""],

    ["resolutions.title", "Goals", ""],
    ["resolutions.placeholder.new_main", "Add New Item...", "..."],
    ["resolutions.placeholder.new_sub", "Add New Subitem...", "..."],
    ["resolutions.action.add", "Add", ""],
    ["resolutions.action.add_sub", "+", "+"]

]);

i18n
  .use(initReactI18next)
  .init({
    resources: translations,
    lng: "zh", // 
    fallbackLng: "en", // 
    interpolation: {
      escapeValue: false // React 
    }
  });

export default i18n; 